import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    navMenu: [
      {title: 'about', icon: 'fas fa-user'},
      {title: 'skills', icon: 'fas fa-briefcase'},
      {title: 'projects', icon: 'fas fa-project-diagram'},
      {title: 'contact', icon: 'fas fa-envelope'}
    ],
    socialMenu: [
      {title: 'facebook', link: 'https://www.facebook.com/mooveFWD', icon: 'fab fa-facebook-f'},
      {title: 'linkedin', link: 'https://www.linkedin.com/in/whpotgieter/', icon: 'fab fa-linkedin-in'},
      {title: 'github', link: 'https://github.com/williepot', icon: 'fab fa-github-alt'},
      {title: 'npm', link: 'https://www.npmjs.com/~williepotgieter', icon: 'fab fa-npm'},
      {title: 'web', link: 'https://www.moovefwd.com', icon: 'fas fa-home'}
    ],
    sideNav: false
  },
  mutations: {
    changeSideNavState (state, payload) {
      state.sideNav = payload
    }
  },
  actions: {
    toggleSideNav (state, payload) {
      state.commit('changeSideNavState', payload)
    }
  },
  getters: {
    getMenu (state) {
      return state.navMenu
    },
    getSocialMenu (state) {
      return state.socialMenu
    },
    getSideNavState (state) {
      return state.sideNav
    }
  }
})
