<template lang="pug">
  v-toolbar(color="light-blue"
    dark
    prominent
    fixed)
    v-toolbar-title.title.font-pacifico Willie Potgieter
    v-spacer
    v-toolbar-items.hidden-xs-only
      v-btn(flat
        tile
        ripple
        v-for="item in menu"
        :key=" 'navmenu_item_' + item.title") {{ item.title }}
    v-toolbar-side-icon.hidden-sm-and-up(@click="clickBurger()")
</template>

<script>
export default {
  methods: {
    clickBurger () {
      this.$store.dispatch('toggleSideNav', !this.sideNavState)
    }
  },
  computed: {
    menu () {
      return this.$store.getters.getMenu
    },
    sideNavState () {
      return this.$store.getters.getSideNavState
    }
  }
}
</script>

<style lang="sass" scoped>
  .title
    line-height: 3rem !important
</style>
